.label {
    display: flex;
    flex-direction: column;
    width: 235px;
    margin-bottom: 21px;
    font-weight: 400;
    font-size: 17px;
}

.input {
    margin-top: 5px;
    height: 25px;
    font-weight: 400;
    font-size: 13px;
    outline: none;
    border-radius: 5px;
    border: 1px solid rgb(206, 205, 205);
}

.input:focus {
    border: 2px solid black;
}